.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

h2 {
  font-family: Formular, sans-serif;
  font-size: 24px;
  font-weight: 500;
}

.widget-creation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;

  & button {
    display: flex;
    align-items: center;
    width: 44%;
    margin: 12px 0;
    cursor: pointer;
    text-align: left;
    background-color: rgba(66, 98, 255, 1);
    border: none;
    font-family: OpenSans, Arial, Helvetica, sans-serif;
    color: white;
    padding: .4rem;
    font-size: 12px;
    font-weight: 600;
    border-radius: 4px;

    &:hover {
      background-color: rgba(69, 91, 237, 1);
    }

    & > svg {
      width: 14px;
      height: auto;
      fill: white;
      margin-right: 4px;
    }
  }
}
